import { useState, useMemo, useCallback, memo } from 'react';
import {
    ScrollText, Calendar, Filter, Search, AlertTriangle,
    TrendingUp, TrendingDown, ChevronRight, Clock, Building2,
    Receipt, Landmark, Shield, Key, Home, Sparkles
} from 'lucide-react';
import ChartContainer from '../components/ui/ChartContainer';
import StatCard from '../components/ui/StatCard';
import Tabs from '../components/ui/Tabs';
import Modal from '../components/ui/Modal';
import {
    POLICY_CATEGORIES,
    POLICY_IMPACT,
    getPolicyTimeline,
    getCurrentRegulations,
    getRecentPolicies,
    getUpcomingPolicies,
    filterPoliciesByCategory,
    searchPolicies
} from '../data/policyData';
import { formatDate } from '../utils/formatters';

// 카테고리 아이콘 매핑
const categoryIcons = {
    loan: Landmark,
    tax: Receipt,
    subscription: Home,
    regulation: Shield,
    supply: Building2,
    lease: Key,
};

/**
 * 법률/정책 요약 페이지
 * 부동산 관련 정책 타임라인 및 분석
 */
const PolicySummary = memo(() => {
    const [activeTab, setActiveTab] = useState('timeline');
    const [selectedCategory, setSelectedCategory] = useState('all');
    const [searchQuery, setSearchQuery] = useState('');
    const [selectedPolicy, setSelectedPolicy] = useState(null);

    // 데이터
    const allPolicies = useMemo(() => getPolicyTimeline(), []);
    const recentPolicies = useMemo(() => getRecentPolicies(5), []);
    const upcomingPolicies = useMemo(() => getUpcomingPolicies(), []);
    const currentRegulations = useMemo(() => getCurrentRegulations(), []);

    // 필터링된 정책
    const filteredPolicies = useMemo(() => {
        let policies = filterPoliciesByCategory(selectedCategory, allPolicies);
        if (searchQuery.trim()) {
            policies = searchPolicies(searchQuery, policies);
        }
        return policies.sort((a, b) => new Date(b.date) - new Date(a.date));
    }, [allPolicies, selectedCategory, searchQuery]);

    // 통계
    const stats = useMemo(() => {
        const now = new Date();
        const thisYear = allPolicies.filter(p => new Date(p.date).getFullYear() === now.getFullYear());
        const highImpact = allPolicies.filter(p => p.impact === 'high' || p.impact === 'critical');

        return {
            total: allPolicies.length,
            upcoming: upcomingPolicies.length,
            thisYear: thisYear.length,
            highImpact: highImpact.length,
        };
    }, [allPolicies, upcomingPolicies]);

    const tabs = [
        { id: 'timeline', label: '정책 타임라인', icon: Calendar },
        { id: 'current', label: '현행 규제', icon: Shield },
        { id: 'upcoming', label: '예정 정책', icon: Clock },
    ];

    // 정책 상세 모달 열기
    const openPolicyDetail = useCallback((policy) => {
        setSelectedPolicy(policy);
    }, []);

    return (
        <div className="page-container">
            {/* Stats */}
            <div className="grid-stats" style={{ marginBottom: 24 }}>
                <StatCard
                    title="전체 정책"
                    value={stats.total}
                    suffix="건"
                    subtitle="2020년 이후"
                    icon={ScrollText}
                    iconColor="#6366f1"
                    iconBg="var(--color-primary-light)"
                />
                <StatCard
                    title="올해 정책"
                    value={stats.thisYear}
                    suffix="건"
                    subtitle="2026년"
                    icon={Calendar}
                    iconColor="#10b981"
                    iconBg="#d1fae5"
                />
                <StatCard
                    title="예정 정책"
                    value={stats.upcoming}
                    suffix="건"
                    subtitle="시행 예정"
                    icon={Clock}
                    iconColor="#f59e0b"
                    iconBg="#fef3c7"
                />
                <StatCard
                    title="주요 정책"
                    value={stats.highImpact}
                    suffix="건"
                    subtitle="영향도 높음"
                    icon={AlertTriangle}
                    iconColor="#ef4444"
                    iconBg="#fee2e2"
                />
            </div>

            {/* Tabs */}
            <Tabs
                tabs={tabs}
                activeTab={activeTab}
                onChange={setActiveTab}
                style={{ marginBottom: 24 }}
            />

            {/* Tab Content */}
            {activeTab === 'timeline' && (
                <div>
                    {/* Filters */}
                    <div className="card" style={{ marginBottom: 16, padding: '12px 16px' }}>
                        <div style={{ display: 'flex', alignItems: 'center', gap: 16, flexWrap: 'wrap' }}>
                            <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                                <Filter size={16} style={{ color: 'var(--color-text-tertiary)' }} />
                                <span style={{ fontSize: '0.85rem' }}>카테고리:</span>
                            </div>
                            <div style={{ display: 'flex', gap: 8, flexWrap: 'wrap' }}>
                                <button
                                    onClick={() => setSelectedCategory('all')}
                                    className={`btn ${selectedCategory === 'all' ? 'btn-primary' : 'btn-ghost'}`}
                                    style={{ padding: '6px 12px', fontSize: '0.8rem' }}
                                >
                                    전체
                                </button>
                                {Object.entries(POLICY_CATEGORIES).map(([key, { name, color }]) => (
                                    <button
                                        key={key}
                                        onClick={() => setSelectedCategory(key)}
                                        className={`btn ${selectedCategory === key ? 'btn-primary' : 'btn-ghost'}`}
                                        style={{
                                            padding: '6px 12px',
                                            fontSize: '0.8rem',
                                            borderColor: selectedCategory === key ? color : undefined,
                                        }}
                                    >
                                        {name}
                                    </button>
                                ))}
                            </div>
                            <div style={{ flex: 1, minWidth: 200, display: 'flex', alignItems: 'center', gap: 8 }}>
                                <Search size={16} style={{ color: 'var(--color-text-tertiary)' }} />
                                <input
                                    type="text"
                                    value={searchQuery}
                                    onChange={(e) => setSearchQuery(e.target.value)}
                                    placeholder="정책 검색..."
                                    style={{ flex: 1, fontSize: '0.85rem' }}
                                />
                            </div>
                        </div>
                    </div>

                    {/* Policy Timeline */}
                    <div style={{ display: 'flex', flexDirection: 'column', gap: 12 }}>
                        {filteredPolicies.map((policy) => {
                            const CategoryIcon = categoryIcons[policy.category] || ScrollText;
                            const categoryInfo = POLICY_CATEGORIES[policy.category];
                            const impactInfo = POLICY_IMPACT[policy.impact];

                            return (
                                <div
                                    key={policy.id}
                                    className="card"
                                    style={{
                                        display: 'flex',
                                        alignItems: 'flex-start',
                                        gap: 16,
                                        padding: 16,
                                        cursor: 'pointer',
                                        transition: 'all var(--transition-fast)',
                                        borderLeft: `4px solid ${categoryInfo?.color || '#6366f1'}`,
                                    }}
                                    onClick={() => openPolicyDetail(policy)}
                                >
                                    <div style={{
                                        width: 40,
                                        height: 40,
                                        borderRadius: 'var(--radius-md)',
                                        background: `${categoryInfo?.color}20`,
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                        flexShrink: 0,
                                    }}>
                                        <CategoryIcon size={20} style={{ color: categoryInfo?.color }} />
                                    </div>
                                    <div style={{ flex: 1 }}>
                                        <div style={{ display: 'flex', alignItems: 'center', gap: 8, marginBottom: 4 }}>
                                            <h4 style={{ fontWeight: 600 }}>{policy.title}</h4>
                                            <span style={{
                                                fontSize: '0.7rem',
                                                padding: '2px 8px',
                                                borderRadius: 'var(--radius-sm)',
                                                background: `${impactInfo?.color}20`,
                                                color: impactInfo?.color,
                                                fontWeight: 600,
                                            }}>
                                                {impactInfo?.name}
                                            </span>
                                        </div>
                                        <p style={{ fontSize: '0.85rem', color: 'var(--color-text-secondary)', marginBottom: 8 }}>
                                            {policy.summary}
                                        </p>
                                        <div style={{ display: 'flex', alignItems: 'center', gap: 12, fontSize: '0.75rem', color: 'var(--color-text-tertiary)' }}>
                                            <span style={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                                                <Calendar size={12} />
                                                {formatDate(policy.date, 'long')}
                                            </span>
                                            <span style={{
                                                padding: '2px 6px',
                                                borderRadius: 'var(--radius-sm)',
                                                background: 'var(--color-bg-tertiary)',
                                            }}>
                                                {categoryInfo?.name}
                                            </span>
                                        </div>
                                    </div>
                                    <ChevronRight size={20} style={{ color: 'var(--color-text-tertiary)' }} />
                                </div>
                            );
                        })}
                    </div>
                </div>
            )}

            {activeTab === 'current' && (
                <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(350px, 1fr))', gap: 24 }}>
                    {currentRegulations.map((group, index) => (
                        <div key={index} className="card">
                            <h3 style={{ fontSize: '1rem', fontWeight: 600, marginBottom: 16 }}>
                                {group.category} 규제
                            </h3>
                            <div style={{ display: 'flex', flexDirection: 'column', gap: 12 }}>
                                {group.items.map((item, idx) => (
                                    <div
                                        key={idx}
                                        style={{
                                            padding: 12,
                                            background: 'var(--color-bg-tertiary)',
                                            borderRadius: 'var(--radius-md)',
                                        }}
                                    >
                                        <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: 4 }}>
                                            <span style={{ fontWeight: 600, color: 'var(--color-primary)' }}>
                                                {item.name}
                                            </span>
                                            <span style={{
                                                fontSize: '0.75rem',
                                                padding: '2px 6px',
                                                borderRadius: 'var(--radius-sm)',
                                                background: 'var(--color-bg-secondary)',
                                                color: 'var(--color-text-tertiary)',
                                            }}>
                                                {item.area}
                                            </span>
                                        </div>
                                        <p style={{ fontSize: '0.8rem', color: 'var(--color-text-secondary)' }}>
                                            {item.description}
                                        </p>
                                    </div>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>
            )}

            {activeTab === 'upcoming' && (
                <div>
                    {upcomingPolicies.length === 0 ? (
                        <div className="card" style={{ textAlign: 'center', padding: 48 }}>
                            <Clock size={48} style={{ color: 'var(--color-text-tertiary)', marginBottom: 16 }} />
                            <p style={{ color: 'var(--color-text-secondary)' }}>예정된 정책이 없습니다</p>
                        </div>
                    ) : (
                        <div style={{ display: 'flex', flexDirection: 'column', gap: 16 }}>
                            {upcomingPolicies.map((policy) => {
                                const CategoryIcon = categoryIcons[policy.category] || ScrollText;
                                const categoryInfo = POLICY_CATEGORIES[policy.category];
                                const daysUntil = Math.ceil((new Date(policy.date) - new Date()) / (1000 * 60 * 60 * 24));

                                return (
                                    <div
                                        key={policy.id}
                                        className="card"
                                        style={{
                                            display: 'flex',
                                            alignItems: 'center',
                                            gap: 20,
                                            padding: 20,
                                            cursor: 'pointer',
                                            background: 'linear-gradient(135deg, var(--color-bg-primary), var(--color-bg-secondary))',
                                        }}
                                        onClick={() => openPolicyDetail(policy)}
                                    >
                                        <div style={{
                                            width: 60,
                                            height: 60,
                                            borderRadius: 'var(--radius-lg)',
                                            background: `${categoryInfo?.color}15`,
                                            border: `2px solid ${categoryInfo?.color}`,
                                            display: 'flex',
                                            flexDirection: 'column',
                                            alignItems: 'center',
                                            justifyContent: 'center',
                                        }}>
                                            <span style={{ fontSize: '1.25rem', fontWeight: 700, color: categoryInfo?.color }}>
                                                D-{daysUntil}
                                            </span>
                                        </div>
                                        <div style={{ flex: 1 }}>
                                            <h4 style={{ fontWeight: 600, fontSize: '1.05rem', marginBottom: 4 }}>
                                                {policy.title}
                                            </h4>
                                            <p style={{ fontSize: '0.85rem', color: 'var(--color-text-secondary)' }}>
                                                {policy.summary}
                                            </p>
                                            <div style={{ marginTop: 8, fontSize: '0.8rem', color: 'var(--color-text-tertiary)' }}>
                                                시행일: {formatDate(policy.date, 'long')}
                                            </div>
                                        </div>
                                        <ChevronRight size={24} style={{ color: 'var(--color-text-tertiary)' }} />
                                    </div>
                                );
                            })}
                        </div>
                    )}
                </div>
            )}

            {/* Policy Detail Modal */}
            {selectedPolicy && (
                <Modal
                    isOpen={!!selectedPolicy}
                    onClose={() => setSelectedPolicy(null)}
                    title={selectedPolicy.title}
                    icon={categoryIcons[selectedPolicy.category] || ScrollText}
                    size="lg"
                >
                    <div style={{ display: 'flex', flexDirection: 'column', gap: 16 }}>
                        <div style={{ display: 'flex', gap: 8 }}>
                            <span style={{
                                padding: '4px 12px',
                                borderRadius: 'var(--radius-sm)',
                                background: `${POLICY_CATEGORIES[selectedPolicy.category]?.color}20`,
                                color: POLICY_CATEGORIES[selectedPolicy.category]?.color,
                                fontSize: '0.8rem',
                                fontWeight: 600,
                            }}>
                                {POLICY_CATEGORIES[selectedPolicy.category]?.name}
                            </span>
                            <span style={{
                                padding: '4px 12px',
                                borderRadius: 'var(--radius-sm)',
                                background: `${POLICY_IMPACT[selectedPolicy.impact]?.color}20`,
                                color: POLICY_IMPACT[selectedPolicy.impact]?.color,
                                fontSize: '0.8rem',
                                fontWeight: 600,
                            }}>
                                영향도: {POLICY_IMPACT[selectedPolicy.impact]?.name}
                            </span>
                        </div>

                        <div style={{ padding: 16, background: 'var(--color-bg-tertiary)', borderRadius: 'var(--radius-md)' }}>
                            <h4 style={{ fontSize: '0.85rem', fontWeight: 600, marginBottom: 8, color: 'var(--color-text-secondary)' }}>
                                요약
                            </h4>
                            <p style={{ lineHeight: 1.6 }}>{selectedPolicy.summary}</p>
                        </div>

                        <div style={{ padding: 16, background: 'var(--color-bg-tertiary)', borderRadius: 'var(--radius-md)' }}>
                            <h4 style={{ fontSize: '0.85rem', fontWeight: 600, marginBottom: 8, color: 'var(--color-text-secondary)' }}>
                                상세 내용
                            </h4>
                            <pre style={{
                                whiteSpace: 'pre-wrap',
                                fontFamily: 'inherit',
                                lineHeight: 1.8,
                                fontSize: '0.9rem',
                            }}>
                                {selectedPolicy.details.trim()}
                            </pre>
                        </div>

                        {selectedPolicy.affectedAreas && (
                            <div>
                                <h4 style={{ fontSize: '0.85rem', fontWeight: 600, marginBottom: 8, color: 'var(--color-text-secondary)' }}>
                                    적용 지역
                                </h4>
                                <div style={{ display: 'flex', gap: 8, flexWrap: 'wrap' }}>
                                    {selectedPolicy.affectedAreas.map((area, idx) => (
                                        <span
                                            key={idx}
                                            style={{
                                                padding: '4px 12px',
                                                borderRadius: 'var(--radius-sm)',
                                                background: 'var(--color-bg-tertiary)',
                                                fontSize: '0.8rem',
                                            }}
                                        >
                                            {area}
                                        </span>
                                    ))}
                                </div>
                            </div>
                        )}

                        {selectedPolicy.marketEffect && (
                            <div style={{
                                padding: 16,
                                background: 'var(--color-warning-light)',
                                borderRadius: 'var(--radius-md)',
                                borderLeft: '4px solid var(--color-warning)',
                            }}>
                                <h4 style={{ fontSize: '0.85rem', fontWeight: 600, marginBottom: 4, color: 'var(--color-warning)' }}>
                                    예상 시장 영향
                                </h4>
                                <p style={{ fontSize: '0.9rem' }}>{selectedPolicy.marketEffect}</p>
                            </div>
                        )}

                        <div style={{
                            display: 'flex',
                            alignItems: 'center',
                            gap: 8,
                            fontSize: '0.8rem',
                            color: 'var(--color-text-tertiary)',
                            paddingTop: 8,
                            borderTop: '1px solid var(--color-border)',
                        }}>
                            <Calendar size={14} />
                            <span>시행일: {formatDate(selectedPolicy.date, 'full')}</span>
                        </div>
                    </div>
                </Modal>
            )}
        </div>
    );
});

PolicySummary.displayName = 'PolicySummary';

export default PolicySummary;
