/**
 * 부동산 정책 데이터 모듈
 * 주요 정책 타임라인 및 카테고리 분류
 * 
 * 성능 최적화: 데이터 캐싱 적용
 */

// 데이터 캐시 저장소
const policyCache = new Map();

// 캐시된 데이터 반환 또는 생성
const getCachedData = (key, generator) => {
    if (policyCache.has(key)) {
        return policyCache.get(key);
    }
    const data = generator();
    policyCache.set(key, data);
    return data;
};

// 캐시 무효화
export const clearPolicyCache = () => {
    policyCache.clear();
};

