// 정책 검색
export const searchPolicies = (query, policies = getPolicyTimeline()) => {
    const lowercaseQuery = query.toLowerCase();
    return policies.filter(policy =>
        policy.title.toLowerCase().includes(lowercaseQuery) ||
        policy.summary.toLowerCase().includes(lowercaseQuery) ||
        policy.details.toLowerCase().includes(lowercaseQuery)
    );
};

// 카테고리별 정책 필터
export const filterPoliciesByCategory = (category, policies = getPolicyTimeline()) => {
    if (!category || category === 'all') return policies;
    return policies.filter(policy => policy.category === category);
};

// 최근 정책 가져오기
export const getRecentPolicies = (count = 5, policies = getPolicyTimeline()) => {
    const now = new Date();
    return policies
        .filter(policy => new Date(policy.date) <= now)
        .sort((a, b) => new Date(b.date) - new Date(a.date))
        .slice(0, count);
};

// 예정된 정책 가져오기
export const getUpcomingPolicies = (policies = getPolicyTimeline()) => {
    const now = new Date();
    return policies
        .filter(policy => new Date(policy.date) > now)
        .sort((a, b) => new Date(a.date) - new Date(b.date));
};
